.btn {
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
  font-family: "IBM Plex Sans", sans-serif;
  font-style: normal;
  font-size: 12px;
  font-weight: 500;
  text-rendering: optimizeLegibility;
  background-color: white;
  cursor: pointer;
  transition: all 0.5s ease;
  padding: 12px 24px;
}

.btn-edit {
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
}

.btn-edit:hover {
  background: var(--primary-color);
  color: #fff;
}

.btn-delete {
  color: var(--secondary-color);
  border: 1px solid var(--secondary-color);
}

.btn-delete:hover {
  background: var(--secondary-color);
  color: #fff;
}

.table-wrapper {
  text-align: center;
}

.btn-add {
  width: 6rem;
  height: 2rem;
  background: var(--primary-color);
  border: none;
  color: #fff;
}

input {
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
}

body {
  background-color: white;
  margin: 0 auto;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1rem;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

/* page hidden for SPA */
.page {
  display: none;
  animation: fadeIn 0.3s;
}

.hide {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.4s, opacity 0.4s linear;
}

.wrapper {
  width: 100%;
  display: grid;
  min-height: 100vh;
  grid-template-rows: 180px auto 64px;
  grid-template-columns: 180px auto;
  grid-template-areas: "topbar topbar" "content content" "navbar navbar";
}

main {
  background-color: #e6e6e6;
  grid-area: content;
}

.topbar {
  grid-area: topbar;
}

.navbar {
  grid-area: navbar;
  display: flex;
  background-color: white;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}

.nav-links {
  display: flex;
  gap: 20px;
  width: 100%;
  justify-content: space-evenly;
}
.nav-links a {
  color: black;
  text-decoration: none;
  text-align: center;
  display: flex;
  flex-direction: column;
}
.nav-links a i {
  display: block;
  margin-bottom: 4px;
}
.nav-links__text {
  font-family: "IBM Plex Sans", sans-serif;
  font-style: normal;
  font-size: 8px;
  line-height: 160%;
  font-weight: 500;
  text-rendering: optimizeLegibility;
}

.event__list {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.card {
  background-color: white;
